body {
	background: #FFF;
	line-height: 1.4;
	min-width: 320px; }

.inner {
	@include sm {
		padding: 0 25px; } }

.title-l {
	font-weight: 600;
	font-size: 45px;
	line-height: 1.2;
	font-family: $cormorant_infant;
	color: $brown;
	@include sm {
		line-height: 1;
		font-size: 25px; }
	&_mod {
		font-weight: 600;
		font-size: 45px;
		line-height: 1.2;
		font-family: $cormorant_infant;
		color: $white;
		@include sm {
			font-size: 30px; } }
	@include sm {
		font-size: 25px; } }

.title-m {
	font-weight: 600;
	font-size: 30px;
	line-height: 1.1;
	font-family: $cormorant_infant;
	color: $brown;
	@include sm {
		line-height: 1;
		font-size: 20px!important; } }

.title-s {
	font-weight: 700;
	font-size: 24px;
	line-height: 1;
	font-family: $cormorant_infant;
	color: $brown;
	@include sm {
		font-family: $tahoma;
		font-size: 16px!important; } }


.title-sm {
	font-family: $cormorant_infant;
	@include f(20px, 700, 1.2);
	text-transform: uppercase; }

.btn {
	position: relative;
	padding: 11px 25px;
	background-color: $gray_brown;
	border-radius: 5px;
	font-size: 14px;
	font-weight: 400;
	font-family: $tahoma;
	color: $brown;
	transition: color .4s, background-color .4s, border-color .4s, box-shadow .4s;
	&:hover {
		background-color: $hover_btn;
		color: $white; }
	@include sm {
		padding: 8px 20px; } }

.btn_border {
	box-shadow: inset 0 0 0 2px $brown_orange;
	background-color: transparent;
	&:hover {
		background-color: #6a6455;
		color: $white;
		box-shadow: inset 0 0 0 2px #6a6455; } }

.line {
	height: 1px;
	width: 100%;
	background-color: rgba(#ffffff,.5);
	margin-bottom: 20px;
	@include sm {
		background-color: rgba(#d9c8ab,.2); } }

.mob-hide {
	@include sm {
		display: none !important; } }

.page_head {
	border-bottom: 1px solid rgba(#f1ede8,.5); }

.hidein {
	@include sm {
		display: none; } }

.showin {
	display: none;
	@include sm {
		display: block; } }

.bcrumbs {
	font-family: $tahoma;
	font-weight: 400;
	font-size: 14px;
	color: $brown; }

.side-form .input-checkbox {
	margin-bottom: 0; }

.side-form .cbox label:after {
	top: 3px;
	width: 14px;
	height: 14px;
	background-color: $silver;
	z-index: 1; }

.side-form .cbox label:before {
	top: 5px;
	left: -14px;
	width: 10px;
	height: 10px;
	content: '';
	border: 1px solid #ccc;
	z-index: 2;
	background-image: repeating-linear-gradient(to bottom right,rgb(192, 192, 192) 0%,rgb(255, 255, 255) 100%);
	background-image: -ms-repeating-linear-gradient(top left,rgb(192, 192, 192) 0%,rgb(255, 255, 255) 100%); }

.padding-aside {
	padding-top: 22px; }

.banner {
	width: 100%;
	img {
		max-width: 100%; } }
