}

aside .cbox label span {
  width: 12px;
  height: 12px;
  display: inline-block;
  margin: 0 7px 0 5px;
}

aside .cbox label:after {
  content: "";
  width: 12px;
  height: 12px;
  border: 1px solid #cccccc;
  position: absolute;
  left: -16px;
  top: 0.5px;
}

